<template>
<!--  -->
    <div class="container-fluid row">
        <form class="per_info container">
            <div class="form-group">
                <label for="userName"></label>
                <input type="text" class="form-control" id="userName" placeholder="" v-model="userInfo.userName">
            </div>
            <div class="form-group">
                <label for="email"></label>
                <input type="email" class="form-control" id="email" placeholder="" v-model="userInfo.email">
            </div>
            <div class="form-group">
                <label for="name"></label>
                <input type="text" class="form-control" id="name" placeholder="" v-model="userInfo.name">
            </div>
            <div class="form-group">
                <label for="tel"></label>
                <input type="text" class="form-control" id="tel" placeholder="" v-model="userInfo.tel">
            </div>
            <div class="form-group">
                <label for="notes"></label>
                <textarea type="text" class="form-control" id="notes" rows=4 placeholder="" v-model="userInfo.notes"/>
            </div>
        </form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            userInfo: {}
        };
    },
    mounted() {},
    methods: {}
};
</script>
<style lang="less" scoped>
.per_info {
    // padding: 20px 30px;
}
</style>






